import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getUserBookings } from '../redux/public/PublicAction'
import '../../assets/css/style.css'
import carImage from '../../assets/image/imageOne.png'

const ViewOrders = () => {
    const dispatch = useDispatch()

    const { info, userBookingstotal } = useSelector(state => state.public)
    console.log(userBookingstotal)
    useEffect(() => {
        dispatch(getUserBookings())
    }, [])
    return <div className='viewCoursebg p-5'>
        <div className="container">
            <div className="row">
                <div className="col-sm-6">
                    {
                        userBookingstotal && userBookingstotal.map(item => <div class="card mt-3 shadow-lg p-2">
                            <div class="card-header ">
                                <div className='d-flex justify-content-between'>
                                    <p>Course Type </p>
                                    <p className='text-dark fw-bold'>{item.course.coursetype}</p>
                                </div>
                            </div>
                            <div class="card-body">
                                <div className='d-flex justify-content-between'>
                                    <p>Course Fee</p>
                                    <button className='btn btn-success  fw-bold '>{item.course.Coursefee}</button>
                                </div>
                                <div className='d-flex justify-content-between mt-2'>
                                    <p>Daily Hourse</p>
                                    <button className='btn btn-   fw-bold '>{item.course.dailyhourse}</button>
                                </div>
                                <div className='d-flex justify-content-between mt-2'>
                                    <p>Vehicle</p>
                                    <button className='btn btn-   fw-bold '>{item.course.vehicle}</button>
                                </div>
                            </div>
                        </div>
                        )
                    }
                </div>
                <div className="col-sm-6 mt-3">
                    {/* <div className="card"> */}
                    <img className='img-fluid' src={carImage} alt="" />
                    {/* </div> */}
                </div>
            </div>
        </div>
    </div>
}

export default ViewOrders